import guessyoulike from "../../components/guessyoulike/guessyoulike.vue"
import uniRate from "@/components/uni-ui/uni-rate/uni-rate.vue"
import Notice from "../../components/Notice/Notice.vue"
export default {
	components: {
		uniRate,
		Notice,
		guessyoulike
	},
	data() {
		return {
			banner: [], //轮播图
			indicatorDots: true,
			disabled:true,
			autoplay: true,
			movie: [], //热门英超
			interval: 2000,
			duration: 500,
			like: []
		}
	},
	onPullDownRefresh: function() {
		// 猜你喜欢
		uni.request({
			url: `${this.$url}/index/guessULike?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				console.log(res.data.data);
				this.like = res.data.data
				uni.stopPullDownRefresh()
			},
			fail: (e) => {
				console.log(e);
			},
			complete: () => {}
		});
	},
	onLoad() {
		// 头部轮播图
		uni.request({
			url: `${this.$url}/index/carousel/list?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				// console.log(res.data);
				this.banner = res.data.data
				console.log(this.banner);
			},
			fail: (e) => {
				console.log(e);
			},
			complete: () => {}
		});
		// 热门英超
		uni.request({
			url: `${this.$url}/index/movie/hot?qq=122212489&type=superhero`,
			method: 'POST',
			data: {},
			success: res => {
				this.movie = res.data.data
				console.log(this.movie);
			},
			fail: (e) => {
				console.log(e);
			},
			complete: () => {}
		});
		// 猜你喜欢
		uni.request({
			url: `${this.$url}/index/guessULike?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				console.log(res.data.data);
				this.like = res.data.data
			},
			fail: (e) => {
				console.log(e);
			},
			complete: () => {}
		});
	},
	methods: {
		todetails(item) {
			console.log(item);
			let obj = JSON.stringify(item)
			uni.navigateTo({
				url: `../details/details?item=${obj}`
			})
		},
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		}
	}
}
